.switch {
  text-align: center;
}

.switch h2 {
  margin: 0;
  font-size: 1rem;
}

.toggleSwitch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 30px;
}

.toggleSwitch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--blue);
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 34px
}

.slider:before {
  position: absolute;
  content: "";
  height: 22px;
  width: 22px;
  left: 4px;
  bottom: 4px;
  background-color: var(--red);
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%;
}

input:checked+.slider {
  background-color: var(--blue);
}

input:focus+.slider {
  box-shadow: 0 0 1px var(--blue);
}

input:checked+.slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}